import React from 'react';
import { Container, Form, Button, Row, Col, Card } from 'react-bootstrap';
import Swal from 'sweetalert2';
import { useData } from '../components/DataLoader';
import { useLanguage } from '../components/LanguageProvider';


const Contact = () => {
  const { data, loading } = useData();
  const {language} = useLanguage();
  
  // Get the first row of data and the headers
  const contactData = data.contact?.[0] || {};
  const headers = Object.keys(contactData || {});


  const onSubmit = async (event) => {
    event.preventDefault();
    const formData = new FormData(event.target);
    formData.append("access_key", "8901efb7-f355-4b35-b993-947bf8dd2cc8");
    const object = Object.fromEntries(formData);
    const json = JSON.stringify(object);
    
    try {
      const res = await fetch("https://api.web3forms.com/submit", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json"
        },
        body: json
      }).then((res) => res.json());

      if (res.success) {
        Swal.fire({
          title: language !== 'fr' ? "Success!" : "Succès!",
          text:  language !== 'fr' ? "The mail has been sent!" : "Le mail a été envoyé!",
          icon: "success"
        });
      }
    } catch (error) {
      console.error("Error submitting form:", error);
      Swal.fire({
        title: language !== 'fr' ? "Error!" : "Erreur!",
        text: language !== 'fr' ? "An error has occurred while sending the message" : "Une erreur est survenue lors de l'envoi du message.",
        icon: "error"
      });
    }
  };

  if (loading) {
    return <div> {language !== 'fr' ? "Loading..." : "Chargement..."}</div>;
  }

  // Helper function to render content paragraphs
  const renderContent = (content) => {
    if (!content) return null;
    return content.split('\n').map((line, index, array) => (
      <p key={index} className={index < array.length - 1 ? 'mb-1' : 'mb-0'}>
        {line.trim()}
      </p>
    ));
  };

  return (
    <>
      <Container className="my-5">
        <h1 className="mb-4">{language !== 'fr' ? "Contact us" : "Contactez-nous"}</h1>
        <br />
        <Form onSubmit={onSubmit} className="mb-5">
          <Form.Group className="mb-3" controlId="formName">
            <Form.Label>{language !== 'fr' ? "Name" : "Nom"}</Form.Label>
            <Form.Control name="name" type="text" placeholder={language !== 'fr' ? "Enter your name" :"Entrez votre nom"} required />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formEmail">
            <Form.Label>{language !== 'fr' ? "Email address" : "Adresse email"}</Form.Label>
            <Form.Control name="email" type="email" placeholder={language !== 'fr' ? "Enter your email" :"Entrez votre email"} required />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formMessage">
            <Form.Label>Message</Form.Label>
            <Form.Control name="message" as="textarea" rows={3} placeholder={language !== 'fr' ? "Enter your message" :"Entrez votre message"} required/>
          </Form.Group>
          <Button variant="primary" type="submit">
            {language !== 'fr' ? "Send" : "Envoyer"}
          </Button>
        </Form>
      </Container>

      <Container fluid className="my-5 p-0" style={{ backgroundColor: '#F1F2FA' }}>
        <Container className="my-5">
          <Row className="mb-5">
            <Col md={7}>
              <br />
              <br />
              <h2>{language !== 'fr' ? "How to find us" : "Où nous trouver"} </h2>
              <hr className="border-3 border-primary opacity-50" />
            </Col>
          </Row>

          <Row>
            <Col md={7}>

              <Card className="border-0 shadow-sm mb-4">
                <Card.Body>
                  <h2 className="h4 mb-4 text-primary">{headers[0]}</h2>
                  {renderContent(contactData[headers[0]])}
                  
                </Card.Body>
              </Card>

              <Row>
                <Col md={6}>
                  <Card className="border-0 shadow-sm mb-4 h-100">
                    <Card.Body>
                      {/* Address and Hours sections */}
                      {[1, 2].map((index) => (
                        <div key={headers[index]}>
                          <h3 className="h4 mb-3 text-primary">{headers[index]}</h3>
                          {renderContent(contactData[headers[index]])}
                          <br />
                        </div>
                      ))}
                    </Card.Body>
                  </Card>
                </Col>

                <Col md={6}>
                  <Card className="border-0 shadow-sm mb-4 h-100">
                    <Card.Body>
                      {/* Email and Phone sections */}
                      {[3, 4].map((index) => (
                        <div key={headers[index]}>
                          <h3 className="h4 mb-3 text-primary">{headers[index]}</h3>
                          {renderContent(contactData[headers[index]])}
                          <br />
                        </div>
                      ))}
                    </Card.Body>
                  </Card>
                </Col>
              </Row>
            </Col>

            <Col md={5} className="d-flex align-items-center">
              <div className="ratio ratio-1x1 h-60">
                <iframe 
                  src={contactData.map_url}
                  className="border-0"
                  allowFullScreen=""
                  loading="lazy"
                  referrerPolicy="no-referrer-when-downgrade"
                  title="Location map"
                ></iframe>
              </div>
            </Col>
          </Row>
        </Container>
        <br />
      </Container>
    </>
  );
};

export default Contact;